@font-face {
  font-family: "Brown";
  src: url("/fonts/Brown-Regular.woff");
  src: url("/fonts/Brown-Regular.woff2");
}
* {
  box-sizing: border-box;
  font-family: "Brown", "Helvetica";
}

body {
  display: flex;
  justify-content: center;
  flex-direction: column;
  height: auto;
  width: 100%;
  margin: 0;
}

.oceana-welcome-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  width: 100%;
}
.oceana-welcome-container svg {
  width: 85%;
  margin-bottom: 12rem;
}
.oceana-welcome-container h2 {
  color: #004b66;
  margin-bottom: -1.4rem;
}

.grid-container-section1, .grid-container-section2, .grid-container-section3 {
  display: grid;
  justify-content: center;
  grid-template-rows: repeat(4 auto);
  height: 100vh;
  width: 100%;
}
.grid-container-section1 .grid-section1, .grid-container-section2 .grid-section1, .grid-container-section3 .grid-section1, .grid-container-section2 .grid-section2, .grid-container-section3 .grid-section2, .grid-container-section3 .grid-section3 {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0rem 1rem;
}
.grid-container-section1 .item1, .grid-container-section2 .item1, .grid-container-section3 .item1 {
  justify-content: space-between;
  height: 4rem;
  width: 100vw;
}
.grid-container-section1 .liseberg-logo-svg, .grid-container-section2 .liseberg-logo-svg, .grid-container-section3 .liseberg-logo-svg {
  margin-left: 25%;
}
.grid-container-section1 .item2, .grid-container-section2 .item2, .grid-container-section3 .item2 {
  position: relative;
  height: 35rem;
  padding: 0;
  margin: 0;
  border: none;
  background-image: url("/images/wave_s2.svg");
  background-repeat: no-repeat;
  background-size: cover;
}
.grid-container-section1 .item2 svg, .grid-container-section2 .item2 svg, .grid-container-section3 .item2 svg {
  object-fit: cover;
}
.grid-container-section1 .page-2-content-container, .grid-container-section2 .page-2-content-container, .grid-container-section3 .page-2-content-container {
  display: flex;
  align-items: center;
  position: absolute;
  height: 32rem;
  width: calc(100vw - 4rem);
  background-color: #e3f5ff;
  border: none;
  flex-direction: column;
}
.grid-container-section1 .page-2-content-container svg, .grid-container-section2 .page-2-content-container svg, .grid-container-section3 .page-2-content-container svg {
  margin-top: 1rem;
}
.grid-container-section1 .page-2-content-container h2, .grid-container-section2 .page-2-content-container h2, .grid-container-section3 .page-2-content-container h2 {
  color: #004b66;
  width: 20rem;
  text-align: center;
  font-weight: bold;
  font-size: 36px;
}
.grid-container-section1 .page-2-content-container h4, .grid-container-section2 .page-2-content-container h4, .grid-container-section3 .page-2-content-container h4 {
  margin: -0.5rem 0rem 0rem 1rem;
  font-weight: bold;
  width: 20rem;
  font-size: 18px;
  color: #fe5c6f;
}
.grid-container-section1 .page-2-content-container p, .grid-container-section2 .page-2-content-container p, .grid-container-section3 .page-2-content-container p {
  margin-left: 1rem;
  line-height: 133.8%;
  margin-top: 0rem;
  width: 20rem;
  font-size: 18px;
  color: #004b66;
}
.grid-container-section1 .page-2-content-container button, .grid-container-section2 .page-2-content-container button, .grid-container-section3 .page-2-content-container button {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 2rem;
  width: 10rem;
  height: 3rem;
  background-color: #004b66;
  color: white;
  font-size: 16px;
  margin-top: 2rem;
  border: none;
}
.grid-container-section1 .item4, .grid-container-section2 .item4, .grid-container-section3 .item4 {
  height: auto;
}

.grid-container-section2, .grid-container-section3 {
  width: 100vw;
  color: #004b66;
}
.grid-container-section2 .grid-section2, .grid-container-section3 .grid-section2, .grid-container-section3 .grid-section3 {
  width: 100vw;
  grid-template-rows: 2;
}
.grid-container-section2 .s2-item1, .grid-container-section3 .s2-item1 {
  justify-content: space-between;
  margin-top: -1.5rem;
  padding: 0rem 2rem 0rem 2rem;
  height: 20vh;
  width: 100%;
}
.grid-container-section2 .s2-item2, .grid-container-section3 .s2-item2 {
  justify-content: space-between;
  flex-direction: column;
  height: 80vh;
}
.grid-container-section2 h2, .grid-container-section3 h2 {
  font-size: 36px;
  text-align: center;
}
.grid-container-section2 p, .grid-container-section3 p {
  margin-top: -2.5rem;
  width: 90%;
  line-height: 21.06px;
}
.grid-container-section2 button, .grid-container-section3 button {
  background-color: white;
  border: 0.2rem solid #004b66;
  color: #004b66;
  border-radius: 3rem;
  height: 3.4rem;
  width: 8rem;
  margin-bottom: 4rem;
  font-size: 18px;
}

.grid-container-section3 {
  height: 100vh;
}
.grid-container-section3 .grid-section3 {
  height: 50vh;
}
.grid-container-section3 .s3-item1 {
  display: flex;
  justify-content: initial;
  flex-direction: column;
  border: 2rem solid #96c0d6;
  background-color: #e3f5ff;
}
.grid-container-section3 .s3-item1 h2 {
  color: #fe5c6f;
  font-size: 44px;
  padding: 0.5rem 0rem 2rem;
}
.grid-container-section3 .s3-item1 b {
  color: #fe5c6f;
}
.grid-container-section3 .s3-item2 {
  flex-direction: column;
  background-color: #004b66;
  color: white;
  justify-content: space-between;
}
.grid-container-section3 .s3-item2 .input-email {
  height: 3rem;
  width: 95%;
  border: none;
  box-shadow: 2px 5px 20px -4px rgba(0, 0, 0, 0.62);
  -webkit-box-shadow: 2px 5px 20px -4px rgba(0, 0, 0, 0.62);
  -moz-box-shadow: 2px 5px 20px -4px rgba(0, 0, 0, 0.62);
}
.grid-container-section3 .s3-item2 button {
  width: 10rem;
  margin-bottom: 1rem;
  margin-left: 50%;
}